.App {
  text-align: center;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.red {
  color: red;
}

.bold {
  font-weight: bold;
}

.App button {
   font: inherit;
   border-image: linear-gradient(150deg,  #eee, #ccc) 1 ;
   padding: 10px 15px;
   margin: 1rem 0;
   cursor: pointer;
   background-color: green;
   color: white;
}
      
.App button:hover {
  background-color:lightgreen;
  color: #000;
}

.App button.Red {
  background-color: red;
}
.App button.Red:hover {
  background-color: salmon;
}